.directorist-gutenberg-listing-card-thumbnail {
	display: grid;
	overflow: hidden;
	border-radius: 12px;
	min-height: 300px;
	width: 100%;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	img.directorist-gutenberg-listing-card-thumbnail-preview-img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.directorist-gutenberg-listing-card-thumbnail-back {
		grid-area: 1 / 1;
		pointer-events: none;
		z-index: 1;
	}

	.directorist-gutenberg-listing-card-thumbnail-front {
		grid-area: 1 / 1;
		z-index: 2;
		pointer-events: auto;
		position: relative;
		display: flex;
		flex-direction: column;
	}
}